.review-form {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;

    .upload-input {
        .contents {
            width: 100%;
            height: 100px;
            padding: 10px;
            resize: none;
            margin-bottom: 10px;
            border: 1px solid #cbcbcb;
            background-color: #f4f4f4;
            box-sizing: border-box;
            font-size: 13px;
            line-height: 18px;
        }
    }

    .btn-upload {
        margin-top: 10px;
        width: 100%;
        height: 30px;
        font-size: 15px;
        color: white;
        background-color: #58ACFA;
    }
}